body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: auto;
  color: var(--textColor);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  text-decoration: none;
}

.text {
  color: var(--textColor);
}

button.defaultBtn:hover {
  cursor: pointer;
  background-color: var(--primaryHover);
  transition-duration: 0.3s;
}

:root {
  --main-bg-color: #fff;
  --blue: #fa100a;
  --element-bg-color: #192734;
  --hover-color: #1c2732;
  --hover-blue-color: blue;
  --border-color: #d9d9d9;
  --text-color: #000;
  --gray: #8899a6;
  --input-background-color: #253341;
  --primary: #fa100a;
  --from-message-color: #3d5466;
  --secondary: #191919;
  --primaryHover: #ba0b06;
  --main-bg-color: #000;
  --blue: #fa100a;
  --element-bg-color: #fa100a;
  --hover-color: rgba(0, 0, 0, 0.03);
  --hoverBg: rgb(255 255 255 / 12%);
  --hover-blue-color: #191919;
  --border-color: #38444d;
  --gray: #191919;
  --input-background-color: #fa100a;
  --from-message-color: #3d5466;
  --third: #000;
  --red: #fa100a;
  --border: #d9d9d9;
  --feedBorder: #d9d9d9;
  --bg: #fff;
  --bg-secondary: #e8e8e8e8e8e8;
  --SidebarIcon: #000;
  --input-bg: rgba(0, 0, 0, 0.03);
  --bgHover: #000;
  --text: #000;
  --bgSecondary: #e8e8e8;
  --bgSecondary2: #fff;
  --bgMenu: #e8e8e8;
  --textColor: #000;
  --username: #191919;
  --label: #000;
  --hover: rgba(0, 0, 0, 0.03);
  --text-color: #000;
  --textPrimary: #fa100a;
  --svgIcon: #fa100a;
  --border: #1e1e1e;
  --borderInput: #fff;
}

li.MuiButtonBase-root.MuiListItem-root.MuiMenuItem-root.MuiMenuItem-gutters.MuiListItem-gutters.MuiListItem-button {
  color: var(--textColor);
}

p.textPrimary {
  color: var(--textPrimary);
  font-weight: 700;
  padding-inline: 4px;
}

@media (prefers-color-scheme: Dark) {
  :root {
    --main-bg-color: #000;
    --blue: #fa100a;
    --element-bg-color: #191919;
    --hover-color: #6e767d;
    --text-color: #fff;
    --text-color: #ffff;
    --hover-blue-color: #191919;
    --border-color: #1e1e1e;
    --input-bg: #191919;
    --textPrimary: #fa100a;

    --gray: #8899a6;
    --input-background-color: #191919;
    --from-message-color: #3d5466;
    --primary: #fa100a;
    --secondary: #191919;
    --third: #949494;
    --red: #fa100a;
    --border: #1e1e1e;
    --text: #fff !important;
    --bgHover: #000;
    --hoverBg: #191919;
    --bgMenu: #191919;
    --label: #fff;
    --hoverBg: rgb(255 255 255 / 12%);
    --bg: #000;
    --text: #fff;
    --bgSecondary: #191919;
    --bgSecondary2: #000;
    --SidebarIcon: #fff;
    --textColor: #fff;
    --text: #fff;
    --hover: #232323;
    --username: #6e767d;
    --feedBorder: #1e1e1e;
    --svgIcon: #fff;
    --borderInput: #000;
  }
}

body {
  background-color: var(--bg);
  background-color: var(--main-bg-color);
  overflow-y: hidden;
  width: 100vw;
  box-sizing: border-box;
}

@media (min-width: 0) and (max-width: 350px) {
  * {
    font-size: 0.9rem !important;
  }
}

.container {
  flex: 1 1;
  flex-direction: row;
  height: 100%;
}

.col1 {
  flex: 0.5 1;
  position: relative;
  background-color: #5aafe4;
}

.logo {
  width: 50%;
  height: 100vh;
  border-color: #fa100a;
  display: inline;
  fill: #fa100a;
}

.info {
  position: absolute;
  top: 0;
  left: 20%;
  right: 20%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 15px 0;
}

.info div:first-child,
.info div:nth-child(2) {
  font-size: 15px;
  margin: 0 0 40px;
}

.info div:nth-child(3) {
  font-size: 15px;
}

.indexIcon {
  width: 19px;
  fill: #fff;
  stroke: #fff;
}

.info div span {
  color: var(--textColor);
  font-size: 19px;
  font-weight: 700;
}

.col2 {
  flex: 0.5 1;
  background-color: var(--bg);
  flex-direction: column;
  justify-content: center;
  padding: 15px;
}

.col2,
.menu {
  display: flex;
}

.menu {
  flex-direction: column;
  margin-left: 20vh;
  margin-right: 20vh;
  min-width: 60%px;
}

.header {
  font-size: 30px;
}

.header,
.join {
  color: var(--textColor);
  font-weight: 700;
}

.join {
  margin: 59px 0 15px;
  font-size: 15px;
}

.buttons,
.signup {
  display: flex;
  flex-direction: column;
}

.signup {
  align-items: stretch;
  background-color: #fa100a;
  border-radius: 9999px;
  border: 1px solid #000;
  margin: 0 0 15px;
  padding: 13px 30px;
}

.signupItem {
  align-items: center;
  display: flex;
  justify-content: center;
  text-decoration: none;
}

.signupItem,
.signupText {
  color: #fff;
  text-align: center;
}

.signupText {
  display: inline;
  font-size: 15px;
  font-weight: 700;
}

.login {
  align-items: stretch;
  background-color: var(--bg);
  border-radius: 9999px;
  border: 1px solid #fa100a;
  display: flex;
  flex-direction: column;
  margin: 0 0 15px;
  padding: 13px 30px;
}

.loginItem {
  align-items: center;
  display: flex;
  justify-content: center;
  text-align: center;
}

.loginItem,
.loginText {
  color: #fa100a;
}

@media (min-width: 0) and (max-width: 700px) {
  .container {
    display: flex;
    flex-direction: column;
    height: none;
    overflow-y: scroll;
  }

  .col1 {
    flex: 1 1;
  }

  .info div span {
    font-size: 15px;
  }

  .col2 {
    flex: 1 1;
  }

  .menu {
    margin: 20px 10vh;
  }
}

.textInputRow {
  border: 1px solid #fff;
  border-radius: 5px;
  margin: 10px 0;
  padding: 0 8px;
  position: relative;
}

.textInputRowActive {
  border: 2px solid var(--primary);
}

.textInput {
  background-color: var(--main-bg-color);
  border: none;
  color: #fff;
  font-size: 16px;
  padding: 16px 0 10px;
  width: 100%;
  outline: 0;
}

.textInputLabel {
  font-size: 16x;
  position: absolute;
  transform-origin: top left;
  transform: translateY(10px) scale(1);
  transition: all 0.1s ease-in-out;
}

.textInputLabelActive {
  transform: translateY(4px) scale(0.75);
}

.container {
  display: flex;
  background-color: var(--bg);
  height: 100vh;
}

.panel {
  margin: 15px auto auto;
  max-width: 400px;
  padding: 0 15px;
}

.panelHeader {
  align-items: stretch;
  display: flex;
  flex-direction: column;
  margin: 0 15px;
}

.headerText {
  flex: 0.5;
  font-size: 20px;
  font-weight: 800;
  color: var(--textColor);
}

.panelHeaderText {
  color: var(--textColor);
  display: inline;
  font-size: 30px;
  font-weight: 700;
  line-height: 39.375px;
  font-family: Chirp, Verdana, System;
  margin-top: 36px;
}

.inputs {
  padding: 0 15px;
}

.loginBtn {
  background-color: #fa100a;
  border-radius: 9999px;
  border: 1px solid #000;
  display: flex;
  flex-direction: column;
  margin: 10px;
  padding-top: 15px;
  padding-bottom: 15px;
  text-decoration: none;
}

.loginText {
  color: #fff;
  font-weight: 700;
}

.loginLinks,
.loginText {
  font-size: 15px;
  text-align: center;
}

.loginLinks {
  margin-top: 30px;
}

.link {
  color: #fa100a;
  text-decoration: none;
}

.link:hover {
  color: var(--primaryHover);
  transition-duration: 0.3s;
  text-decoration: none;
}

.point {
  color: var(--textColor);
  margin: 5px;
}

.signUpContainer {
  height: 100vh;
  background-color: #191919;
  display: flex;
  justify-content: center;
  overflow-y: scroll;
}

.signUpContainer::-webkit-scrollbar {
  display: none;
}

.card {
  background-color: var(--bg);
  width: 540px;
  min-height: 600px;
  margin-top: 37px;
  margin-bottom: 37px;
  padding-left: 30px;
  padding-right: 30px;
  border-radius: 10px;
}

.signuplogo {
  display: flex;
  justify-content: center;
}

.signupHeader {
  color: var(--textColor);
  font-size: 23px;
  font-weight: 700;
  line-height: 30.1875px;
  margin: 15px 0;
}

.acceptTerm {
  color: var(--textColor);
  font-size: 15px;
  grid-gap: normal;
  gap: normal;
  margin: 59px 0 0;
}

.acceptTermBlue {
  color: #fa100a;
}

.signupBtn {
  background-color: #fa100a;
  border-radius: 9999px;
  border: 1px solid #000;
  display: flex;
  flex-direction: column;
  margin: 15px 0 0;
  padding-top: 15px;
  padding-bottom: 15px;
  text-decoration: none;
}

@media (min-width: 0) and (max-width: 700px) {
  .signUpContainer {
    height: auto;
  }

  .card,
  .signUpContainer {
    background-color: var(--bg);
  }

  .card {
    margin: 0;
  }
}

.feed {
  flex: 0.6 1;
  border-right: 0.5px solid var(--border-color);
  border-left: 0.5px solid var(--border-color);
  min-height: 100vh;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

.feed-header {
  position: sticky;
  top: 0;
  background-color: var(--main-bg-color);
  z-index: 100;
  border-bottom: 0.5px solid var(--border-color);
  padding: 0 15px;
  display: flex;
  flex: 1 1;
  max-height: 50px;
  min-height: 50px;
  align-items: center;
}

.feed-headerText {
  flex: 0.5 1;
  font-size: 20px;
  font-weight: 800;
  color: var(--textColor);
}

.homeColumn {
  flex: 0.5 1;
  display: flex;
  justify-content: flex-end;
}

.homeIcon {
  fill: var(--primary);
  padding: 8px;
  margin: 8px 0;
}

.homeStars:hover {
  background-color: var(--hoverBg);
  border-radius: 9999px;
}

.feed-header .MuiAvatar-root {
  display: none;
}

@media (min-width: 0) and (max-width: 1000px) {
  .feed {
    flex: 1 1;
  }
}

@media (min-width: 0) and (max-width: 450px) {
  .feed {
    flex: 1 1;
    margin: 0;
  }

  .feed-header .MuiAvatar-root {
    display: block;
    margin-right: 20px;
    height: 32px;
    width: 32px;
  }

  .feed > .bottomBorder,
  .feed > .tweetbox {
    display: none;
  }

  .drawerBarPanel {
    width: 100vw;
    min-height: 100vh;
    background-color: var(--gray) 0.8;
    position: absolute;
    z-index: 1000;
  }
}

.tweetbox {
  margin-top: 10px;
  padding: 10px;
  display: flex;
  flex: 1 1;
  flex-direction: column;
}

.bottomBorder,
.tweetbox {
  border-bottom: 1px solid var(--border-color);
}

.bottomBorder {
  height: 8px;
  background-color: var(--element-bg-color);
}

.tweetboxRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex: 1 1;
}

.tweetboxUserIcon {
  flex: 0.1 1;
}

.tweetbox-input-row {
  display: flex;
  flex-direction: column;
  flex: 0.9 1;
}

.tweetbox-input {
  flex: 1 1;
  border-radius: 10px;
  border: none;
  background-color: var(--main-bg-color);
  color: #fff;
  font-size: 19px;
  line-height: 24.9375px;
}

.tweetboxOptions {
  flex: 0.9 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  fill: var(--primary);
}

.tweetboxOptionIcon {
  padding: 10px;
}

.tweetboxOptionIcon:hover {
  background-color: var(--hoverBg);
  border-radius: 9999px;
}

.tweetbox-button {
  margin-top: 5px;
  margin-left: auto;
  background-color: var(--primary);
  border-radius: 9999px;
  color: #fff;
  border: none;

  font-weight: 900;
  font-size: 15px;
  padding: 10px 15px;
}

a.flex.items-center {
  color: var(--primary);
}

.tweetbox-button:hover {
  margin-top: 5px;
  margin-left: auto;
  background-color: var(--primaryHover);
  border-radius: 9999px;
  color: #fff;
  border: none;

  font-weight: 900;
  font-size: 15px;
  padding: 10px 15px;
}

.post {
  padding-top: 10px;
  border-bottom: 0.5px solid var(--border-color);
  display: flex;
  flex-direction: row;
  position: relative;
}

.post:hover {
  background-color: var(--hover-color);
}

.post > div > .MuiAvatar-root {
  width: 50px;
  height: 50px;
  margin: 0 10px 0 15px;
}

.post-content-col {
  margin-right: 15px;
  flex: 1 1;
  position: relative;
}

.post-header {
  display: flex;
  align-items: center;
}

.post-header-displayname {
  color: #fff;
  font-size: 15px;
  font-weight: 700;
  margin-right: 5px;
}

.post-header-displayname:hover {
  text-decoration: underline;
}

.post-header-date,
.post-header-username {
  color: var(--username);
  font-size: 15px;
}

.post-header-date {
  margin-left: 5px;
}

.postMoreIcon {
  margin-top: -10px;
  margin-left: auto;
  margin-right: -5px;
  color: var(--gray);
  border-radius: 9999px;
  padding: 5px;
}

.postMoreIcon:hover {
  background-color: var(--hoverBg);
  color: var(--primary);
}

.post-content {
  text-align: justify;
  color: #fff;
  display: inline;
  font-size: 15px;
}

.post-event {
  display: flex;
  flex: 1 1;
}

.post-event > div {
  flex: 0.25 1;
  display: flex;
  align-items: center;
}

.post-event > div > span {
  color: var(--gray);
  font-size: 13px;
}

.postIcon {
  fill: var(--gray);
  width: 18px;
  padding: 10px;
  border-radius: 9999px;
}

.post-event div:first-child .postIcon:hover,
.post-event div:nth-child(4) .postIcon:hover {
  fill: var(--primary);
  background-color: var(--hoverBg);
}

.post-event div:first-child .postIcon:hover + span {
  color: var(--primary);
}

.post-event div:nth-child(2) .postIcon:hover {
  fill: #17bf63;
  background-color: rgba(23, 191, 99, 0.1);
}

.post-event div:nth-child(2) .postIcon:hover + span {
  color: #17bf63;
}

.post-event div:nth-child(3) .postIcon:hover {
  fill: #e0245e;
  background-color: rgba(224, 36, 94, 0.1);
}

.post-event div:nth-child(3) .postIcon:hover + span {
  color: #e0245e;
}

.post-image {
  padding-top: 10px;
}

.post-image img {
  width: 100%;
  border-radius: 10px;
  max-height: 300px;
  object-fit: cover;
}

.profileDetailCard {
  display: flex;
  flex-direction: column;
  z-index: 5;
  position: absolute;
  top: 50px;
  left: 30px;
  background-color: var(--main-bg-color);
  border-radius: 30px;
  padding: 15px;
  width: 300px;
  box-shadow: 0 0 8px var(--gray);
  animation-duration: 1s;
  animation-name: profileCardAnimate;
}

@keyframes profileCardAnimate {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.bottomSidebar {
  display: none;
}

@media (min-width: 0) and (max-width: 450px) {
  .bottomSidebar {
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    width: 100%;
    z-index: 9999;

    display: flex;
    flex-direction: row;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    background-color: var(--main-bg-color);
    border-top: 1px solid var(--border-color);
  }

  .bottomSidebar > div {
    flex: 0.25 1;
    display: flex;
    align-items: center;
    z-index: 9999;

    justify-content: center;
  }
}

.sidebarItem {
  display: flex;
  align-items: center;
  cursor: pointer;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
}

.sidebarItem > .MuiSvgIcon-root {
  padding: 15px;
}

.sidebarItem > span {
  margin-right: 20px;
  color: #fff;
  font-size: 19px;
  font-weight: 700;
  line-height: 24.9375px;
}

.sidebarItem:hover {
  border-radius: 30px;
  color: var(--primary);
  transition: color 0.1s ease-out;
  background-color: var(--hoverBg);
}

.sidebarItem:hover > span {
  color: var(--primary);
}

.sidebarItem--active > .sidebaricon,
.sidebarItem:hover > .sidebaricon {
  color: var(--primary);
  fill: var(--primary);
}

.sidebarItem--active > span {
  color: var(--primary);
}

.sidebaricon {
  color: var(--text);
  fill: var(--text);
  padding: 15px;
  width: 24px;
}

@media (min-width: 0) and (max-width: 1250px) {
  .sidebaricon {
    padding: 10px;
    width: 24px;
  }

  .sidebarItem > .MuiSvgIcon-root {
    padding: 10px;
  }
}

.moreMenuItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-decoration: none;
}

.moreMenuItem > svg {
  height: 19px;
  width: 19px;
  fill: var(--svgIcon);
  padding: 15px;
}

.moreMenuItem > span {
  color: var(--text);
  font-size: 15px;
  font-family: system-ui, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-weight: 500;
}

.moreMenuItem:hover {
  background-color: var(--hover-color);
}

@media (min-width: 0) and (max-width: 450px) {
  .drawerBar {
    display: flex;
    flex-direction: column;
    width: 280px;
    height: 100vh;
    transform: translateX(-100%);
    position: absolute;
    background-color: var(--main-bg-color);
    z-index: 1001;
    transition: transform 0.3s linear;
  }

  .drawerBar.active {
    transform: translateX(0);
  }

  .drawerBarHeader {
    display: flex;
    flex-direction: row;
    padding: 15px;
    border-bottom: 0.5px solid var(--border-color);
  }

  .drawerBarHeader > span:first-child,
  .drawerBarHeader > span:nth-child(2) {
    font-size: 19px;
    font-weight: 800;
    color: #fff;
  }

  .drawerBarHeader > span:nth-child(2) {
    margin-left: auto;
  }

  .draweBarScroll {
    overflow-y: scroll;
    height: 100vh;
  }

  .drawerBarProfile {
    display: flex;
    flex-direction: column;
    padding: 15px;
    overflow-y: scroll;
  }

  .drawerBarProfile > div {
    display: flex;
    flex-direction: row;
  }

  .drawerBarProfile .MuiAvatar-root {
    width: 40px;
    height: 40px;
  }

  .drawerBarProfile svg {
    width: 30px;
    height: 30px;
    margin-left: auto;
    fill: var(--primary);
    border-radius: 9999px;
  }

  .drawerBarProfile svg:hover {
    background-color: var(--hover-color);
  }

  .drawerBarProfile > span:nth-child(2) {
    margin-top: 10px;
    font-size: 15px;
    font-weight: 700;
    color: #fff;
  }

  .drawerBarProfile > span:nth-child(3) {
    font-size: 15px;
    color: var(--textColor);
  }

  .drawerBarProfile div:nth-child(4) {
    margin-top: 20px;
  }

  .drawerBarProfile div:nth-child(4) span {
    margin-right: 15px;
  }

  .drawerBarProfile div:nth-child(4) span span:first-child {
    color: #fff;
    font-weight: 700;
    font-size: 15px;
    margin-right: 5px;
  }

  .drawerBarProfile div:nth-child(4) span span:nth-child(2) {
    color: var(--textColor);
    font-size: 15px;
  }

  .draweBarScroll > .moreMenuItem:nth-child(7),
  .draweBarScroll > .moreMenuItem:nth-child(9),
  .draweBarScroll > .moreMenuItem:nth-child(11),
  .draweBarScroll > .moreMenuItem:nth-child(14) {
    border-bottom: 0.2px solid var(--gray);
  }

  .draweBarScroll > .moreMenuItem:nth-child(15) > svg {
